import {mapState,mapMutations,mapGetters} from 'vuex';
import {CHANGECOUNTMUTATION,CHANGESELECTMUTATION,CHANGESELECTEDALLMUTATION} from '../../store/mutation-type.js';
export default {
	data() {
		return {
			userInfo:{}
		}
	},
	computed:{
		...mapState({
			cartList:state=>state.cart.cartList
		}),
		...mapGetters(["isSelectAll","totalPrice","totalNum"])
	},
	beforeMount(){
		let userInfo=JSON.parse(wx.getStorageSync("userInfo")||"{}");
		this.userInfo=userInfo;
	},
	methods:{
		...mapMutations({
			changeCountMutation:CHANGECOUNTMUTATION,
			changeSelectMutation:CHANGESELECTMUTATION,
			changeSelectedAllMutation:CHANGESELECTEDALLMUTATION
		}),
		toLogin(){
			wx.navigateTo({
				url:"/pages/login/login"
			})
		},
		changeCount(isAdd,index){
			// console.log(index)
			this.changeCountMutation({isAdd,index})
		},
		changeSelect(selected,index){
			this.changeSelectMutation({selected,index})
		},
		changeSelectedAll(selected){
			this.changeSelectedAllMutation(selected)
		}
	}
}
